body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#app-container {
  max-height: 100vh;
  overflow: hidden; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.auth-login {
  max-width: 400px; }

.note-list-container .rendered-notes {
  max-height: calc(100vh - 230px);
  min-height: calc(100vh - 230px);
  overflow-y: auto; }

.note-list-container .list-group-item-action {
  cursor: pointer; }

.note-viewer-container .save-progress {
  position: relative;
  top: -8px;
  visibility: hidden; }
  .note-viewer-container .save-progress.show-progress {
    visibility: visible; }

.note-viewer-container .btn-deletenote {
  cursor: pointer; }
  .note-viewer-container .btn-deletenote.disabled {
    cursor: not-allowed; }

.note-viewer-container .react-quill-wrapper {
  height: calc(100vh - 221px); }
  .note-viewer-container .react-quill-wrapper .quill {
    height: 100%; }
    .note-viewer-container .react-quill-wrapper .quill .ql-container {
      height: calc(100% - 42.13px); }
    .note-viewer-container .react-quill-wrapper .quill .ql-toolbar.ql-snow {
      background-color: white; }
      .note-viewer-container .react-quill-wrapper .quill .ql-toolbar.ql-snow .ql-header.ql-picker {
        color: black; }
        .note-viewer-container .react-quill-wrapper .quill .ql-toolbar.ql-snow .ql-header.ql-picker .ql-picker-label {
          color: black; }
        .note-viewer-container .react-quill-wrapper .quill .ql-toolbar.ql-snow .ql-header.ql-picker svg > * {
          stroke: black; }
        .note-viewer-container .react-quill-wrapper .quill .ql-toolbar.ql-snow .ql-header.ql-picker:hover:not(.ql-expanded) .ql-picker-label {
          color: #06c; }
        .note-viewer-container .react-quill-wrapper .quill .ql-toolbar.ql-snow .ql-header.ql-picker:hover:not(.ql-expanded) svg > * {
          stroke: #06c; }

@media (max-width: 747px) {
  .note-viewer-container .react-quill-wrapper .quill .ql-container {
    height: calc(100% - 66.25px); } }

